import { settings } from '../../utils/constants';
import { View } from './view';
export class PageView extends View {
    constructor(container, events) {
        super(container, events);
        this.container = container;
        this.events = events;
        this.productsInBusket = container.querySelector(settings.pageSettings.productsInBusket);
        this.products = container.querySelector(settings.pageSettings.products);
        this._wrapper = container.querySelector(settings.pageSettings.wrapper);
        this._button = container.querySelector(settings.contactsSettings.button);
    }
    set locked(isOpened) {
        if (isOpened) {
            this._wrapper.classList.add('page__wrapper_locked');
        }
        else {
            this._wrapper.classList.remove('page__wrapper_locked');
        }
    }
    render(data) {
        if (data) {
        }
        return this.container;
    }
}
